y-right-bottom-panel,\n.mercury-right-top-panel {\n  overflow-y: auto;\n}\n\n.widget-text {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n}\n\n.widget-text input {\n  width: 100%;\n  box-sizing: border-box; /* ensures padding and border are included in width */\n  padding: 8px;\n  margin-top: 4px;\n}\n\n.mercury-main-panel > .lm-SplitPanel-handle {\n  background: lightgrey;\n  width: 0px !important;\n  cursor: ew-resize;\n}\n\n.mercury-panel-with-margin {\n  /* max-width: 1280px; */\n  max-width: 1024px;\n  margin-left: auto;\n  margin-right: auto;\n  padding-left: 24px;\n  padding-right: 24px;\n  box-sizing: border-box;\n}\n\n/*********************************************************/\n/* Mercury App Toolbar */\n\n.mercury-toolbar-title {\n  width: 150px;\n  border: 1px solid var(--jp-border-color2);\n  border-radius: 4px;\n  padding: 2px 6px;\n  margin-left: 4px;\n  margin-right: 4px;\n  font: inherit;\n  color: var(--jp-ui-font-color1);\n  background: var(--jp-layout-color1);\n  height: 70% !important;\n  font-size: 0.8rem !important;\n}\n\n.mercury-toolbar-desc {\n  width: 150px;\n  border: 1px solid var(--jp-border-color2);\n  border-radius: 4px;\n  padding: 2px 6px;\n  margin-left: 4px;\n  margin-right: 4px;\n  font: inherit;\n  color: var(--jp-ui-font-color1);\n  background: var(--jp-layout-color1);\n  height: 70% !important;\n  font-size: 0.8rem !important;\n}\n\n.mercury-toolbar-checkbox {\n  display: inline-flex;\n  align-items: center;\n  margin-left: 4px;\n  margin-right: 4px;\n  font-size: 0.8rem;\n  color: var(--jp-ui-font-color1);\n}\n\n/* Mercury App Toolbar – Thumbnail Text */\n.mercury-toolbar-thumbtext {\n  width: 100px; /* enough for ≤10 chars */\n  border: 1px solid var(--jp-border-color2);\n  border-radius: 4px;\n  padding: 2px 6px;\n  margin-left: 4px;\n  margin-right: 4px;\n  font: inherit;\n  color: var(--jp-ui-font-color1);\n  background: var(--jp-layout-color1);\n  height: 70% !important;\n  font-size: 0.8rem !important;\n}